@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    box-sizing: border-box;
  }
.banner-container{
    background-color: rgb(0, 0, 0);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 50px;
}
.banner-container img{
    max-width: 80%;
    margin-top: 40px;
    margin-left: 80px;
}
.banner-descrition{
    margin-top: 20%;
}
.banner-descrition h1{color:rgb(218, 210, 218);}
.banner-descrition h4{
    font-style: italic;
    color:rgb(133, 126, 133);
}
.banner-descrition h1{
    font-size: 3.5em;
    font-family: 'Noto Serif', serif;
}
.footer h1{
    font-family: 'Ephesis', cursive;
}
.fab, .fas{
    width: 60px;
    height:60px;
    border-radius: 50%;
    cursor: pointer;
}

* {
    box-sizing: border-box;
  }
.product {
    border: 1px solid rgb(110, 107, 107);
    margin: 10px;
    padding: 15px;
    height: 540px;
    width: 400px;
    border-radius: 10px;
    box-shadow: 5px 7px 35px gray;
}
.product:hover {
    border: 2px solid steelblue;
    box-shadow: 5px 10px 40px lightblue;
}
.product img,
.single-comment img {
    text-align: center;
    border-radius: 10%;
    max-width: 100%;
    height: 300px;
    margin-bottom: 10px;
}
.btn {
    color: #ffffff;
    background: #471f75ef;
    text-shadow: 0 0 10px #ffffff;
    font-size: 17px;
    border-radius: 13px;
}
.btn:hover{
    border: 2px solid rgb(44, 38, 38);
    background: #5f165f;
    color: #ffffff;
}

* {
    box-sizing: border-box;
  }
.products{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
/* -----------------
     Media Queries
-------------------- */
/* Responsiveness for Mobile Devices */
@media only screen and (max-width:414px) {
    .products{
        grid-template-columns: repeat(1, 1fr);
    }
}
@media only screen and (min-width:1000px) and (max-width:768px) {
    .products{
        grid-template-columns: repeat(2, 1fr);
    }
}
* {
    box-sizing: border-box;
  }
.comments-container h1{
    color: rgba(40, 63, 138, 0.534);
}
.comments{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.single-comment{
    margin: 10px;
}
.left-comment{
    border-right: 1px solid darkgrey;
}
.review{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
